import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import ThumbUpIcon from '@mui/icons-material/ThumbUp';
import ThumbDownIcon from '@mui/icons-material/ThumbDown';
import Reply from '@mui/icons-material/Reply';
import SaveAltIcon from '@mui/icons-material/SaveAlt';
import Comments from '../components/Comments';
import ThumbUpOffAltIcon from '@mui/icons-material/ThumbUpOffAlt';
import ThumbDownOffAltIcon from '@mui/icons-material/ThumbDownOffAlt';
import {useDispatch, useSelector} from "react-redux"
import { useLocation } from 'react-router-dom';
import axios from 'axios';
import { fetchStart, fetchFailure, dislike, fetchSuccess, like } from '../redux/videoSlice.js';
import moment from "moment/moment";
import { subscription } from "../redux/userSlice";
import Recommendation from "../components/Recommendation";

const Container = styled.div`
  display: flex;
  gap: 1.5rem;
  padding: 0;
`;

const Content = styled.div`
  flex: 5;
`;

const VideoWrapper = styled.div`

`;

const Title = styled.h1`
  font-size: 1rem;
  font-weight: Bold;
  margin-top: 0.7rem;
  margin-bottom: 0.25rem;
  color: ${({theme}) => theme.text};
`;

const Details = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const Info = styled.span`
  color: ${({theme}) => theme.textSoft};
`;

const Buttons = styled.div`
  display: flex;
  gap: 1rem;
  color: ${({theme}) => theme.text};
`;

const Button = styled.div`
  display: flex;
  align-items: center;
  gap: 5px;
  cursor: pointer;
`;

const Hr = styled.hr`
  margin: 1rem 0;
  border: 0.5px solid ${({theme}) => theme.soft};
`;

const Channel = styled.div`
  display: flex;
  justify-content: space-between;
`;

const ChannelInfo = styled.div`
  display: flex;
  gap: 1rem;
`;

const Image = styled.img`
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
`;

const ChannelDetail = styled.div`
  display: flex;
  flex-direction: column;
  color: ${({ theme }) => theme.text};
`;

const ChannelName = styled.span`
  font-weight: 500;
`;

const ChannelCounter = styled.span`
  margin-top: 0.2rem;
  margin-bottom: 1rem;
  color: ${({ theme }) => theme.textSoft};
  font-size: 0.75rem;
`;

const Description = styled.p`
  font-size: 0.8rem;
`;

const Subscribe = styled.button`
  background-color: #cc1a00;
  font-weight: 500;
  color: white;
  border: none;
  border-radius: 3px;
  height: max-content;
  padding: 0.5rem 1rem;
  cursor: pointer;
`;

const VideoFrame = styled.video`
  max-height: 720px;
  width: 100%;
  object-fit: cover;
`;

const Video = () => {

  const { currentUser } = useSelector((state) => state.user);
  const { currentVideo } = useSelector((state) => state.video);
  const dispatch = useDispatch();
  
  const path = useLocation().pathname.split("/")[2];

  const [channel, setChannel] = useState({});
  const [sharedtext, setSharedtext] = useState('Share');

  useEffect(() => {
    dispatch(fetchStart())
    const fetchData = async () => {
      try {
        const videoRes = await axios.get(`/api/videos/find/${path}`);
        const channelRes = await axios.get(
          `/api/users/find/${videoRes.data.userId}`
        );
        const { password, ...others } = channelRes.data;
        setChannel(others);
        dispatch(fetchSuccess(videoRes.data));
      } catch (error) {
        dispatch(fetchFailure(error));
      }
    };
    fetchData();
    // eslint-disable-next-line
  }, [path, dispatch]);
    
  const handleLike = async () => {
    await axios.put(`/api/users/like/${currentVideo._id}` ,{headers: {
      Authorization: 'Bearer'
    }},{withCredentials : true});
    dispatch(like(currentUser._id));
  };

  const handleDislike = async () => {
    await axios.put(`/api/users/dislike/${currentVideo._id}` ,{headers: {
      Authorization: 'Bearer'
    }},{withCredentials : true});
    dispatch(dislike(currentUser._id));
  };
  
  const handleSub = async () => {
    currentUser?.subscribedUsers?.includes(channel._id)
      ? await axios.put(`/api/users/unsub/${channel._id}`, { headers: { Authorization: 'Bearer' } }, { withCredentials: true })
      : await axios.put(`/api/users/sub/${channel._id}` ,{headers: { Authorization: 'Bearer' }},{withCredentials : true});
    dispatch(subscription(channel._id));
  };

  const handleShare = ()=>{
    setSharedtext("Copied");
    const sharedtext=window.location.href;
    navigator.clipboard.writeText(sharedtext);
    setTimeout(() => {
      setSharedtext("Shared");
    },2000);
  }

  // TODO : Delete video functionality

    
  return (
    <Container>
      <Content>
        <VideoWrapper>
          <VideoFrame src={currentVideo?.videoUrl} controls />
        </VideoWrapper>
        <Title>{currentVideo?.title}</Title>
        <Details>
          <Info>{currentVideo?.views} views | {moment(currentVideo?.createdAt).fromNow()}</Info>
          <Buttons>
            <Button onClick={handleLike}>
              {currentVideo?.likes?.includes(currentUser?._id) ? (<ThumbUpIcon fontSize='small' />) : (<ThumbUpOffAltIcon fontSize='small' />)}{" "} {currentVideo?.likes?.length}
            </Button>
            <Button onClick={handleDislike}>
              {currentVideo?.dislikes?.includes(currentUser?._id) ? (<ThumbDownIcon fontSize='small' />) : (<ThumbDownOffAltIcon fontSize='small' />)}{" "} Dislike
            </Button>
            <Button onClick={handleShare}>
              <Reply fontSize='small' /> {sharedtext}
            </Button>
            <Button>
              <SaveAltIcon fontSize='small' /> Save
            </Button>
          </Buttons>
        </Details>
        <Hr />
        <Channel>
          <ChannelInfo>
            <Image src={channel.img} />
            <ChannelDetail>
              <ChannelName>{channel.name}</ChannelName>
              <ChannelCounter>{channel.subscribers} subscribers</ChannelCounter>
              <Description>{currentVideo?.desc}</Description>
            </ChannelDetail>
          </ChannelInfo>
          <Subscribe onClick={handleSub}>
            {currentUser?.subscribedUsers?.includes(channel._id)
              ? "SUBSCRIBED"
              : "SUBSCRIBE"}
          </Subscribe>
        </Channel>
        <Hr />
        <Comments videoId={currentVideo?._id} />
      </Content>
      <Recommendation tags={currentVideo?.tags} />
    </Container>
  );
};

export default Video;
