import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { Link } from "react-router-dom";
import moment from "moment/moment.js";
import axios from 'axios';

const Container = styled.div`
    width: ${(props) => props.type !== 'sm' && "17rem"};
    margin-bottom: ${(props) => (props.type === 'sm' ? "0.7rem" : "2.5rem")};
    cursor: pointer;
    display: ${(props) => props.type === 'sm' && 'flex'};
    gap: 0.7rem;
`;

const Image = styled.img`
    width: 100%;
    height: ${(props) => (props.type === 'sm' ? '6rem' : '10rem')};
    background-color: #999;
    flex: 1;
`;

const Details = styled.div`
    display: flex;
    margin-top: ${(props) => props.type !== 'sm' && "1rem"};
    gap: 0.75rem;
    flex: 1;
`;

const ChannelImage = styled.img`
    width: 1.75rem;
    height: 1.75rem;
    border-radius: 50%;
    background-color: #999;
    display: ${(props) => props.type === 'sm' && "none"}
`;

const Texts = styled.div``;

const Title = styled.h1`
    font-size: 0.8rem;
    margin: 0;
    font-weight: 600;
    color: ${({theme}) => theme.text};
`;

const ChannelName = styled.h2`
    font-size: 0.7rem;
    color: ${({ theme }) => theme.textSoft};
    margin: 5px 0;
`;

const Info = styled.div`
    font-size: 0.7rem;
    color: ${({ theme }) => theme.textSoft};
`;

const Card = ({ type, video }) => {
    
    const [channel, setChannel] = useState({});

    useEffect(() => {
        const fetchChannel = async () => {
            const res = await axios.get(`/api/users/find/${video.userId}`);
            setChannel(res.data);
        };

        fetchChannel();
    }, [video.userId]);

    const incView = async () => {
        const viewRes = await axios.put(`/api/videos/view/${video._id}`);
    }
    
    return (
        <Link to={`/video/${video._id}`} style={{ textDecoration: "none" }}>
            <Container onClick={incView} type={type}>
                <Image type={type} src={video.imgUrl}></Image>
                <Details type={type}>
                    <ChannelImage type={type} src={channel.img} />
                    <Texts>
                        <Title>{video.title}</Title>
                        <ChannelName>{channel.name}</ChannelName>
                        <Info>{video.views} views | {moment(video.createdAt).fromNow()}</Info>
                    </Texts>
                </Details>
            </Container>
        </Link>
    );
};

export default Card;
