import React from 'react'
import styled from 'styled-components'
import logoImg from "../img/logo.png"
import HomeIcon from '@mui/icons-material/Home';
import ExploreIcon from '@mui/icons-material/Explore';
import SubscriptionsIcon from '@mui/icons-material/Subscriptions';
import LibraryAddIcon from '@mui/icons-material/LibraryAdd';
import HistoryIcon from '@mui/icons-material/History';
import LibraryMusicIcon from '@mui/icons-material/LibraryMusic';
import SportsBasketballIcon from '@mui/icons-material/SportsBasketball';
import SportsEsportsIcon from '@mui/icons-material/SportsEsports';
import MovieCreationIcon from '@mui/icons-material/MovieCreation';
import ArticleIcon from '@mui/icons-material/Article';
import LiveTvIcon from '@mui/icons-material/LiveTv';
import SettingsIcon from '@mui/icons-material/Settings';
import ReportIcon from '@mui/icons-material/Report';
import HelpIcon from '@mui/icons-material/Help';
import LightModeIcon from '@mui/icons-material/LightMode';
import NightlightIcon from '@mui/icons-material/Nightlight';
import LockOpenIcon from '@mui/icons-material/LockOpen';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';

const Container = styled.div`
    flex: 1;
    background-color: ${({theme}) => theme.bgLighter};
    color: ${({theme}) => theme.text};
    height: 100vh;
    position: sticky;
    top: 0;
`;

const Wrapper = styled.div`
    padding: 1rem 1rem;
`;

const Logo = styled.div`
    display: flex;
    align-items: center;
    gap: 1rem;
    font-weight: bold;
    margin-bottom: 1rem;
`;

const Img = styled.img`
    height: 2rem;
`;

const Item = styled.div`
    display: flex;
    align-items: center;
    gap: 1rem;
    cursor: pointer;
    font-size: 0.75rem;
    padding: 0.4rem;

    &:hover{
        background-color: ${({theme}) => theme.soft};
    }
`;

const Hr = styled.hr`
    margin: 0.7rem 0;
    border: 0.5px solid ${({theme}) => theme.soft};
`;

const Login = styled.div`
    font-size: 0.85rem;
`;

const Button = styled.button`
    padding: 0.3rem 0.75rem;
    background-color: transparent;
    border: 1px solid #3ea6ff;
    color: #3ea6ff;
    border-radius: 3px;
    font-weight: 500;
    margin-top: 0.3rem;
    font-weight: bold;
    display: flex;
    align-items: center;
    gap: 5px;
    cursor: pointer;
`;

const Title = styled.h2`
  font-size: 14px;
  font-weight: 500;
  color: #aaaaaa;
  margin-bottom: 20px;
`;

const Menu = ({ darkMode, setDarkMode }) => {

    const { currentUser } = useSelector((state) => state.user);

    return (
        <Container>
            <Wrapper>
                <Link to="/" style={{ textDecoration: "none", color: "inherit" }}>
                    <Logo>
                        <Img src={logoImg} />
                        VTube
                    </Logo>
                </Link>
                <Link to="/" style={{ textDecoration: "none", color: "inherit" }}>
                    <Item>
                        <HomeIcon fontSize='small' />
                        Home
                    </Item>
                </Link>
                <Link to="trend" style={{ textDecoration: "none", color: "inherit" }}>
                    <Item>
                        <ExploreIcon fontSize='small' />
                        Explore
                    </Item>
                </Link>
                <Link to="subscriptions" style={{ textDecoration: "none", color: "inherit" }}>
                    <Item>
                        <SubscriptionsIcon fontSize='small' />
                        Subscriptions
                    </Item>
                </Link>
                <Hr />
                <Item>
                    <LibraryAddIcon fontSize='small' />
                    Library
                </Item>
                <Item>
                    <HistoryIcon fontSize='small' />
                    History
                </Item>
                <Hr />
                {!currentUser && <><Login>
                    Sign in to like videos, comment and subscribe.
                    <br></br>
                    <Link to="signin" style={{ textDecoration: "none" }}>
                        <Button><LockOpenIcon fontSize='small' />SIGN IN</Button>
                    </Link>
                </Login>
                    <Hr /></>}
                <Title>BEST OF VTUBE</Title>
                <Item>
                    <LibraryMusicIcon fontSize='small' />
                    Music
                </Item>
                <Item>
                    <SportsBasketballIcon fontSize='small' />
                    Sports
                </Item>
                <Item>
                    <SportsEsportsIcon fontSize='small' />
                    Gaming
                </Item>
                <Item>
                    <MovieCreationIcon fontSize='small' />
                    Movies
                </Item>
                <Item>
                    <ArticleIcon fontSize='small' />
                    News
                </Item>
                <Item>
                    <LiveTvIcon fontSize='small' />
                    Live
                </Item>
                <Hr />
                <Item>
                    <SettingsIcon fontSize='small' />
                    Settings
                </Item>
                <Item>
                    <ReportIcon fontSize='small' />
                    Report
                </Item>
                <Item>
                    <HelpIcon fontSize='small' />
                    Help
                </Item>
                <Item onClick={() => setDarkMode(!darkMode)}>
                    {darkMode ? <LightModeIcon fontSize='small' /> : <NightlightIcon fontSize='small' />}
                    {darkMode ? <span>Light Mode</span> : <span>Dark Mode</span>}
                </Item>
            </Wrapper>
        </Container>
    );
};

export default Menu;
