import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { useDispatch } from "react-redux";
import { commentFailure, commentStart, commentSuccess } from "../redux/commentSlice";


const Container = styled.div`
    display: flex;
    gap: 0.5rem;
    margin: 1.5rem 0;
`;

const Avatar = styled.img`
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
`;

const Details = styled.div`
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    color: ${({ theme }) => theme.text};
`;

const Name = styled.span`
    font-size: 0.8rem;
    font-weight: 500;
`;

const Date = styled.span`
    font-size: 0.7rem;
    font-weight: 400;
    color: ${({ theme }) => theme.textSoft};
    margin-left: 0.2rem;
`;

const Text = styled.span`
    font-size: 0.8rem;
`;

const ButtonDelete = styled.span`
  background-color: #3d3837;
  font-weight: 500;
  color: white;
  border: none;
  border-radius: 3px;
  height: max-content;
  padding: 10px 20px;
  cursor: pointer;
`;

const Comment = ({ comment }) => {

    const [channel, setChannel] = useState({});
    const dispatch = useDispatch();

    useEffect(() => {
        const fetchComment = async () => {
            const res = await axios.get(`/api/users/find/${comment.userId}`);
            setChannel(res.data);
        };
        fetchComment();
    }, [comment.userId]);

    const handleDelete = async(e) => {
        dispatch(commentStart())
        e.preventDefault();
        try {
          await axios.delete(`/api/comments/${comment._id}`,{headers: {Authorization: 'Bearer'}, withCredentials : true})
          // let commentAfterDelete = comment.filter(comment => comment._id !== comment);
          dispatch(commentSuccess(comment._id))
        } catch (error) {
          dispatch(commentFailure(error))
          console.log(error);
        }
      }

    return (
        <Container>
            <Avatar src={channel.img} />
            <Details>
                <Name>
                    {channel.name} <Date>{moment(comment?.createdAt).fromNow()}</Date>
                </Name>
                <Text>{comment.desc}</Text>
            </Details>
            <ButtonDelete onClick={handleDelete}>
                Delete
            </ButtonDelete>
        </Container>
    );
};

export default Comment;
