import React, {useEffect, useState} from 'react'
import styled from 'styled-components';
import Comment from './Comment';
import { useSelector } from "react-redux";
import axios from "axios";

const Container = styled.div``;

const NewComment = styled.div`
    display: flex;
    align-items: center;
    gap: 0.5rem;
`;

const Avatar = styled.img`
    width: 2.4rem;
    height: 2.4rem;
    border-radius: 50%;
`;

const Input = styled.input`
    border: none;
    border-bottom: 1px solid ${({ theme }) => theme.soft};
    color: ${({ theme }) => theme.text};
    background-color: transparent;
    outline: none;
    padding: 0.2rem;
    width: 100%;
`;

const ButtonComment = styled.button`
background-color: #3d3837;
font-weight: 500;
color: white;
border: none;
border-radius: 3px;
height: max-content;
padding: 10px 20px;
cursor: pointer;
`;

const Comments = ({ videoId }) => {
    
    const { currentUser } = useSelector((state) => state.user);

    const [comments, setComments] = useState([]);
    const currentComment = useSelector((state) => state.comment)
    const [commentInput, setCommentInput] = useState("");

    useEffect(() => {
        const fetchComments = async () => {
            try {
                const res = await axios.get(`/api/comments/${videoId}`);
                setComments(res.data);
            } catch (error) {
                console.log(error);
            }
        };
        fetchComments();
    }, [videoId, currentComment]);

    useEffect(() => {
        const updateDeletedComment = async() =>{
          try {
            const afterDelete = comments.filter(comment => comment._id !== currentComment)
            setComments(afterDelete)
          } catch (error) {
            console.log(error);
          }
        }
        updateDeletedComment();
        // eslint-disable-next-line
      }, [currentComment])

      const handleComment = async(e) =>{
        e.preventDefault();
        const headers = {
          "desc" : commentInput,
          videoId : videoId,
        }
        try {
          const commentData = await axios.post(`/api/comments`, headers,{withCredentials : true})
          setCommentInput("")
          setComments(comments.concat(commentData.data))
        } catch (error) {
          console.log(error);
        }
      }

    return (
        <Container>
            <NewComment>
                <Avatar src={currentUser?.img} />
                <Input placeholder='Add a comment...' onChange={e=> setCommentInput(e.target.value)}/>
                <ButtonComment onClick={handleComment}>Comment</ButtonComment>
            </NewComment>
            {comments.map(comment => (
                <Comment key={comment._id} comment={comment} />
            ))}
        </Container>
    );
};

export default Comments;
